/* Tech Stack / Work Page Specific Styles */

/* Tech stack section with enhanced visuals */
.stack {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin-bottom: 24px;
  padding: 20px;
  background: linear-gradient(135deg, #f5f5f5 0%, #e8e8e8 100%);
  border: 2px solid #808080;
  border-radius: 0 0 8px 8px;
  box-shadow: inset 1px 1px 3px rgba(0, 0, 0, 0.1);
  position: relative;
}

/* Vaporwave theme stack */
.theme-vaporwave .stack {
  background: linear-gradient(135deg, rgba(20, 0, 40, 0.8) 0%, rgba(40, 0, 60, 0.8) 100%);
  border-color: #ff6fd8;
  box-shadow: 0 0 15px rgba(255, 111, 216, 0.2);
}

.theme-vaporwave .stack img {
  background: linear-gradient(180deg, rgba(255, 111, 216, 0.2) 0%, rgba(0, 212, 255, 0.2) 100%);
  border-color: #ff6fd8;
}

.theme-vaporwave .stack img:hover {
  background: linear-gradient(180deg, rgba(0, 212, 255, 0.3) 0%, rgba(255, 111, 216, 0.3) 100%);
  box-shadow: 0 0 15px rgba(0, 212, 255, 0.5);
}

.theme-vaporwave .tech-item {
  color: #e0d0ff;
}

/* Terminal theme stack */
.theme-terminal .stack {
  background: #0a0a0a;
  border: 1px solid #00ff00;
  border-radius: 0;
  box-shadow: 0 0 10px rgba(0, 255, 0, 0.1);
}

.theme-terminal .stack img {
  background: #0d0d0d;
  border: 1px solid #005500;
  filter: grayscale(100%) brightness(0.8);
}

.theme-terminal .stack img:hover {
  filter: grayscale(0%) brightness(1) drop-shadow(0 0 5px rgba(0, 255, 0, 0.5));
  border-color: #00ff00;
  background: rgba(0, 255, 0, 0.1);
}

.theme-terminal .tech-item {
  color: #00ff00;
  font-family: 'Courier New', monospace;
}

p.work {
  font-size: 12px;
  color: #333333;
  line-height: 1.6;
}

/* Tech stack icons with hover effects */
.stack img {
  width: 50px;
  height: 50px;
  padding: 8px;
  margin: 4px;
  background: linear-gradient(180deg, #ffffff 0%, #f0f0f0 100%);
  border: 2px solid;
  border-color: #ffffff #808080 #808080 #ffffff;
  filter: drop-shadow(-1px 1px 2px rgba(0, 0, 0, 0.2));
  transition: all 0.3s;
  cursor: pointer;
}

.stack img:hover {
  width: 60px;
  height: 60px;
  padding: 8px;
  margin: -1px;
  background: linear-gradient(180deg, #f0f8ff 0%, #e0f0ff 100%);
  transform: translateY(-4px) rotate(5deg);
  filter: drop-shadow(-2px 4px 4px rgba(0, 0, 0, 0.3));
  z-index: 10;
  position: relative;
}

.tech-item {
  text-align: center;
}

.category-tabs {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  flex-direction: row;
  align-content: center;
  align-items: flex-end;
  gap: 0;
  margin-bottom: 0px;
  position: relative;
  z-index: 1;
}

/* Tab styling - Classic theme (default) */
.category-tab {
  background: linear-gradient(180deg, #e8e8e8 0%, #d0d0d0 100%);
  border: 2px solid #808080;
  border-bottom: none;
  border-radius: 6px 6px 0 0;
  padding: 6px 12px;
  font-family: 'Segoe UI', 'MS Sans Serif', sans-serif;
  font-size: 11px;
  font-weight: 500;
  color: #333333;
  cursor: pointer;
  transition: all 0.15s ease;
  position: relative;
  margin: 0 1px;
  margin-bottom: -2px;
}

.category-tab:hover {
  background: linear-gradient(180deg, #f0f0f0 0%, #e0e0e0 100%);
  color: #000000;
}

.category-tab.active {
  background: linear-gradient(180deg, #ffffff 0%, #f8f9fa 100%);
  border-color: #808080;
  color: #ff8c00;
  font-weight: 600;
  padding-bottom: 8px;
  z-index: 2;
}

/* Vaporwave theme tabs */
.theme-vaporwave .category-tab {
  background: linear-gradient(180deg, rgba(255, 111, 216, 0.3) 0%, rgba(56, 19, 194, 0.4) 100%);
  border-color: #ff6fd8;
  color: #e0d0ff;
}

.theme-vaporwave .category-tab:hover {
  background: linear-gradient(180deg, rgba(255, 111, 216, 0.5) 0%, rgba(56, 19, 194, 0.5) 100%);
  color: #ffffff;
  box-shadow: 0 0 10px rgba(255, 111, 216, 0.3);
}

.theme-vaporwave .category-tab.active {
  background: linear-gradient(180deg, rgba(0, 212, 255, 0.3) 0%, rgba(20, 0, 40, 0.95) 100%);
  border-color: #00d4ff;
  color: #00d4ff;
  text-shadow: 0 0 8px rgba(0, 212, 255, 0.5);
}

/* Terminal theme tabs */
.theme-terminal .category-tab {
  background: #0a0a0a;
  border: 1px solid #005500;
  border-bottom: none;
  border-radius: 0;
  color: #00aa00;
  font-family: 'Courier New', monospace;
  text-transform: uppercase;
  font-size: 10px;
  padding: 6px 8px;
  letter-spacing: 0;
}

.theme-terminal .category-tab:hover {
  background: rgba(0, 255, 0, 0.1);
  color: #00ff00;
  border-color: #00ff00;
}

.theme-terminal .category-tab.active {
  background: #0d0d0d;
  border-color: #00ff00;
  color: #00ff00;
  text-shadow: 0 0 5px rgba(0, 255, 0, 0.5);
}

/* Other work section */
.other-work {
  margin-top: 24px;
  padding: 16px;
  background: linear-gradient(180deg, #fafafa 0%, #f0f0f0 100%);
  border: 2px dashed #808080;
}

.other-work p {
  font-size: 12px;
  line-height: 1.8;
}

.other-work p * {
  vertical-align: middle;
}

/* Responsive adjustments */
@media screen and (max-width: 540px) {
  .projects {
    min-height: 200px;
    margin: 20px 0;
  }

  .old-projects.welcome ul {
    padding-left: 10px;
  }

  .stack img {
    width: 40px;
    height: 40px;
    padding: 6px;
  }

  .stack img:hover {
    width: 50px;
    height: 50px;
  }

  .category-tabs {
    gap: 2px;
  }

  .category-tab {
    padding: 5px 6px;
    font-size: 9px;
  }

  .category-tab.active {
    padding-bottom: 7px;
  }

  .theme-terminal .category-tab {
    padding: 5px 5px;
    font-size: 8px;
  }
}
